.projects-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin: 0;
    background-color: rgb(222, 204, 174);
  }
  
  .project-card {
    width: 70%;
    margin: 20px;
    padding: 15px;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    background-color: white;
  }
  
  .project-image {
    width: 100%;
    border-radius: 8px;
  }
  
  .project-details {
    margin-top: 10px;
  }
  
  .project-title {
    color: gray;
    font-size: 20px;
    line-height: 30px;
    margin-bottom: 5px;
  }
  
  .project-description {
    font-size: 0.9rem;
    color: #666;
  }
  
  .project-links {
    margin-top: 10px;
  }
  
  .project-links a {
    display: block;
    margin-bottom: 5px;
    text-decoration: none;
    color: #007bff;
  }
  
  .project-links a:hover {
    text-decoration: underline;
  }
  