/* Resume.css */

.resume-container {
    max-width: 800px;
    margin: 50px auto;
    padding: 20px;
    background-color: #ffffff;
    border-radius: 5px;
    box-shadow: -33px 32px 41px -40px rgba(0,0,0,0.75);
  }
  
  header {
    text-align: center;
    margin-bottom: 20px;
  }
  
  header h1 {
    font-size: 2.5em;
    color: #333;
    
  }
  
  header p {
    font-size: 1.5em;
    color: #777;
  }
  
  .resume-details-container {
    display: flex;
    justify-content: space-between;
  }
  
  .resume-details {
    width: 100%;
  }
  
  .resume-details h2 {
    font-size: 1.8em;
    color: #333;
    margin-bottom: 10px;
    padding-right: 5%;
  }
  
  ul {
    list-style: none;
    padding: 0;
  }
  
  li {
    margin-bottom: 5px;
  }
  
  .experience-item,
  .education-item {
    margin-bottom: 20px;
  }
  
  .experience-item h3,
  .education-item h3 {
    font-size: 1.5em;
    color: #333;
    margin-bottom: 5px;
  }
  
  .experience-item p,
  .education-item p {
    font-size: 1.2em;
    color: #777;
  }
  .details{
    width: auto;
    height: auto;
    padding: 0% 3%;
    border-left: solid grey;
  }
  .flex{
    display: flex;
    flex-wrap: wrap;
  }
  .designer{
    z-index: -1;
    width: 50%;
    height:  1500px;
    position: absolute;
    top: 80px;
    left: 0;
    background-color:rgb(235, 219, 198);
  }
  @media only screen and (max-width: 780px) {
    .designer{
        top:150px;
        width: 100%;
        height: 1700px;;
    }
  }