@font-face {
  font-family: 'heading';
  src: url('./fonts/PoiretOne-Regular.ttf')  format('truetype');
}


.react-transition {
    animation-fill-mode: both;
  }

  
@keyframes swipeRight {
    from {
      transform: translate3d(1000px,0,0);
      opacity: 0;
    }
  
    to {
      transform: translate3d(0,0,0);
      opacity: 1;
    }
  }
  
  .swipe-right {
    animation-timing-function: ease-in-out;
    transform-origin: bottom center;
    animation-name: swipeRight;
    animation-duration: 300ms;
  }
  
  
  @keyframes swipeUp {
    from {
      transform: translate3d(0,50vh,0);
      opacity: 0;
    }
  
    to {
      transform: translate3d(0,0vh,0);
      opacity: 1;
    }
  }
  
  .swipe-up {
    animation-timing-function: linear;
    transform-origin: bottom center;
    animation-name: swipeUp;
    animation-duration: 800ms;
  }

  @keyframes swipeLeft {
    from {
      transform: translate3d(-1000px,0,0);
      opacity: 0;
    }
  
    to {
      transform: translate3d(0,0,0);
      opacity: 1;
    }
  }
  
  .swipe-left {
    animation-timing-function: ease-in-out;
    transform-origin: bottom center;
    animation-name: swipeLeft;
    animation-duration: 800ms;
  }
  .intro-main{
      display:flex;
  }
  .intro-container{
    width: 600px;
    height: 600px;
    background-color:rgb(235, 219, 198);
  }
  .introcard{
    box-shadow: -33px 32px 41px -40px rgba(0,0,0,0.75);
    top: 200px;
    left: 25%;
    position: absolute;
    height: auto;
    width: 350px;
    background-color:rgb(241, 237, 230) ;
  }
  .profile-image{
    contain: content;
    background-color: white;
    height:200px;
    width: 200px;
    border-radius: 100px;
    margin: 40px auto;
  }
  .profile-image img{
       width: 200px;
  }
 h2{ 
    text-align: center;
    line-height: 10px;
    color: black;
  }
  .socialmedia{
    width: 250px;
    height: 50px;
    background-color: white;
  }
  .socialmedia{
    padding: 0px 50px 0px 50px ;
    justify-content: space-between;
    align-items: center;
    font-size: larger;
  }
  .introtext-conatiner{
    padding: 4% 12%;
    width: auto;
    height: 500px;
  }
  .introtext-conatiner h1{
    line-height: 20px;
    font-size: 8vw;
  }
  .introtext-conatiner p{
    font-size: 2vw;
  }
  .buttons{
    display: flex;
  }
  .button{
    cursor: pointer;
    font-weight: 600;
    line-height: 35px;
    font-size: 15px;
    border-radius: 20px;
    margin: 10px;
    text-align: center;
    width: 150px;
    height: 40px;
  }
  .button:hover ,.introcard:hover{
    transform: scale(1.05);
  }
  .resume{
    background-color: blue;
    color: white;
  }
  .projects{
    background-color: white;
    border: 1px solid black;

  }
  @media only screen and (max-width: 1200px) {
    
    .introcard{
      left: 5%;
    }
  }
  @media only screen and (max-width: 870px) {
     .intro-container{
      width: 100%;
     }
     .intro-main{
      flex-wrap: wrap;
     }
     .introcard{
      left: 8%;
      justify-content: center;
     }
     
  }