/* Navbar.css */

.navbar-container {
    width: 100%;
    height: 90px;
    line-height: 90px;
    display: flex;
    justify-content: space-between;
  }
  
  .navbar-container h2 {
    line-height: 50px;
    margin-left: 30px;
  }
  
  .link {
    padding: 10px;
    color: gray;
    font-family: system-ui;
    margin-right: 30px;
    text-decoration: none;
  }
  
  .link:hover {
    color: blue;
  }
  
  /* Media Queries for Responsive Design */
  
  @media screen and (max-width: 768px) {
    .navbar-container {
      flex-direction: column;
      height: auto;
      align-items: center;
    }
  
    .navbar-container h2 {
      margin-left: 0;
      margin-bottom: 10px;
    }
  
    .link {
      margin: 10px 0;
    }
  }
  