.openingpage{
    padding: 0;
    margin: 0;
    background-image: url(https://c.wallhere.com/images/83/c3/1cacab872d57c656e586a610d826-1580443.jpg!d);
    width: 100%;
    height: 1064px;
}
.inline{
    position: absolute;
    justify-content: center;
    display: flex;
    flex-wrap: wrap;
}
.visiterForm-container{
    top: 20%;
    right: 10%;
    left: 10%;
    justify-content: center;
    position: absolute;
    width: 80%;
    background-color: rgba(0, 0, 0, 0.396);
    display: flex;
    flex-wrap: wrap;
    border-radius: 20px;
}
.intro-visiterForm{
    font-size: 3.5vw;
    width: 500px;
    margin: 20px;
    padding: 10px;
    color: rgba(255, 255, 255, 0.536);
    background-color: rgba(42, 144, 165, 0);
}
.visiterForm p{
    font-family: system-ui ;
     margin: 5px;
}

.visiterForm{
    width: 500px;
    margin: 10px;
    padding: 10px;
    background-color: rgba(42, 73, 165, 0);
    color: rgb(255, 255, 255);
}

.visiterForm button{
    margin: 20px;
    border: solid skyblue;
    padding: 10px 20px;
    background-color: rgba(255, 255, 255, 0);
    color: white;
    cursor: pointer;
}

.visiterForm button:hover{
    font-weight: 600;
    background-color: skyblue;
}

